.card--course--transparent {
    @include card-course();
}

.card--course--background {
    @include card-course(
        $background-color: $color-gray1,
        $background-hover: $color-gray2
    );
}

.card--course--related-course {
    @include card-course();
    @include card-course-anchor();
}

.card--course--related-course--background {
    @include card-course(
        $background-color: $color-gray1,
        $background-hover: $color-gray2
    );
    @include card-course-anchor();
}
