.card--profile {
    .card--profile__details {
        flex-basis: 100%;
    }

    figure {
        flex-basis: 33%;

        & + .card--profile__details {
            flex-basis: 66%;

            @include media('>mobile') {
                padding-left: $measure--basic;
            }
        }
    }

    .card--profile__jobtitle {
        display: block;
    }
}
