////
/// Figure
/// @group patterns
////
article figure {
    a {
        @include anchor-img();
    }

    figcaption {
        @include smalltype();
        color: $color-text--caption;
        font-weight: $font-weight-book;
        margin-top: $measure--basic;
        margin-bottom: $measure--small2;
        line-height: $measure--small1;

        a {
            @include anchor-discreet();
        }

        > span {
            display: block;
        }
    }

    &.figure--svg {
        img {
            @include media('<=mobile') {
                padding-bottom: 100vw;
            }
        }
    }
}
