@mixin tabs-links-style() {
    .tabs__links {
        align-items: stretch;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        list-style-type: none;
        margin-bottom: 0;
        margin-left: 0;
        margin-top: $measure--basic;
        max-width: unset;
        overflow-x: hidden;
        width: 100%;

        li {
            flex-grow: 1;
            margin-bottom: 0;
            margin-top: 0;
            max-width: unset;
            text-align: center;

            &:not(:first-of-type) {
                border-left-color: $color-bg;
                border-left-width: $border-width-hairline;
                border-left-style: $border-style-solid;
            }

            a {
                @include anchor-span();
                display: block;
                height: 100%;
                padding: $measure--half;
                width: 100%;

                &:not(:hover):not([aria-selected='true']) {
                    background-color: $color-shadow;
                    border-bottom-color: $color-shadow;
                    border-bottom-width: $border-width-hairline;
                    border-bottom-style: $border-style-solid;
                }

                &[aria-selected='true'] {
                    border-left-color: $color-shadow;
                    border-left-width: $border-width-hairline;
                    border-left-style: $border-style-solid;
                    border-right-color: $color-shadow;
                    border-right-width: $border-width-hairline;
                    border-right-style: $border-style-solid;
                    border-top-color: $color-shadow;
                    border-top-width: $border-width-hairline;
                    border-top-style: $border-style-solid;
                    border-bottom: none;
                }

                span {
                    @include h6style();
                }
            }
        }
    }
}

.js .tabs {
    @include media('print') {
        .tabs__links {
            display: none;
        }

        .tabs__panel[hidden] {
            display: block;
        }
    }

    @include media('screen') {
        @include tabs-links-style();

        .tabs__panel {
            border-left-color: $color-shadow;
            border-left-width: $border-width-hairline;
            border-left-style: $border-style-solid;
            border-right-color: $color-shadow;
            border-right-width: $border-width-hairline;
            border-right-style: $border-style-solid;
            border-bottom-color: $color-shadow;
            border-bottom-width: $border-width-hairline;
            border-bottom-style: $border-style-solid;
            padding: $measure--half;
        }

        .tabs__panel__heading {
            margin-top: 0;
        }
    }
}

.js .tabs--accordion {
    @include media('print') {
        .accordion {
            display: none;
        }
    }

    @include media('screen', '<=tablet') {
        .accordion {
            display: block;
        }

        .tabs {
            display: none;
        }
    }

    @include media('screen', '>tablet') {
        .accordion {
            display: none;
        }

        .tabs {
            display: block;
        }
    }
}
