@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin card-course(
    $background-color: transparent,
    $background-hover: $color-gray1
) {
    padding: 1rem; // temp value so the grey background can be seen
    margin-bottom: $measure--small4;
    position: relative;

    &:hover {
        &:after {
            background-color: $background-hover;
            transform: scale(1.03);
        }
    }

    &:after {
        content: '';
        transition: background-color $transition-duration ease-in-out,
            transform $transition-duration ease-in-out;
        background-color: $background-color;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .type {
        color: $color-red50;
        @include smalltype();
        margin-top: $measure--small1;
    }

    p {
        margin: 0 0 $measure--small2 0;
        @include smalltype();
        color: $color-gray10;
    }

    .key-info {
        color: $color-gray7;

        span {
            float: left;
            width: $measure--small3;
            color: $color-red50;
            @include smalltype();
        }

        div {
            margin-left: $measure--small2;
            @include smalltype();
        }
    }
}

@mixin box-shadow {
    box-shadow: 0 $measure--quarter $measure--basic 0 rgba(0, 0, 0, 0.15);
}
