@mixin card-course-anchor() {
    @include anchor-reset();
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: $measure--small1;

    @include media('>tablet') {
        grid-template-columns: $measure--huge2 auto;
    }

    &:hover {
        .title {
            color: $color-gray10;
            background-size: 100% 0.2rem;
        }

        img {
            transform: scale(1.1);
        }

        .image-container {
            &:after {
                background-color: rgba(0, 0, 0, 0.1);
            }
        }
    }

    &:focus,
    &:active {
        &:after {
            @include outline-properties();
        }
    }

    &:visited {
        .title {
            border-bottom-color: $color-inline-link--visited;
            color: $color-inline-link--visited;
        }
    }

    .image-container {
        overflow: hidden;
        width: $measure--medium4;
        height: $measure--medium4;
        position: relative;

        @include media('>tablet') {
            width: $measure--huge2;
            height: $measure--huge2;
        }

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            transition: background-color $transition-duration ease-in-out;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    .title {
        color: $color-gray7;
        padding-bottom: $measure--quarter;
        background-image: linear-gradient($color-gray10, $color-gray10);
        background-position: 0% 100%;
        background-repeat: no-repeat;
        background-size: 0% 0.2rem;
        transition: background-size 0.6s;
    }

    img {
        transition: all $transition-duration ease-in-out;
    }
}
