////
/// Accordion
/// @group patterns
////
@mixin accordion-heading() {
    border-bottom: $border-width-hairline $border-style-solid $color-text;

    button {
        @include anchor-button();
        width: 100%;
        text-align: left;

        div {
            @include ninetyten-grid();
        }

        .accordion__heading__indicator {
            align-self: center;
            text-align: right;

            &:before {
                content: fa-content($fa-var-plus);
            }
        }
    }

    &[data-open='true'] {
        button {
            .accordion__heading__indicator:before {
                content: fa-content($fa-var-minus);
            }
        }

        & + .accordion__body {
            display: block;
        }
    }
}

.js .accordion {
    @include media('screen') {
        & > .accordion__body {
            display: none;
        }

        & > .accordion__heading {
            @include accordion-heading();
        }
    }
}
