.js .pagination__wrapper {
    @include media('print') {
        .pagination__controls,
        .pagination__summary {
            display: none;
        }
    }

    @include media('screen') {
        .pagination__controls {
            align-items: stretch;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            button {
                @include anchor-pagination();
                span {
                    display: inline-block;
                    padding-left: $measure--quarter;
                    padding-right: $measure--quarter;
                }
            }

            .pagination__controls__ellipsis {
                align-items: center;
                display: flex;
                justify-content: center;

                span {
                    display: inline-block;
                    padding-bottom: $border-width-default;
                }
            }

            button,
            .pagination__controls__ellipsis {
                flex-basis: 0;
                flex-grow: 1;
                margin-right: $measure--half;
                margin-top: $measure--threequarters;
                min-height: $minimum-control-height;
                padding-bottom: $measure--quarter;
            }

            [data-page] {
                display: none;

                &[aria-expanded='true'],
                &[data-page='1'],
                &.pagination__controls__button--last {
                    display: block;
                }

                &[data-proximity='1'],
                &[data-proximity='-1'] {
                    @include media('>=tablet') {
                        display: block;
                    }
                }

                &[data-proximity='2'],
                &[data-proximity='-2'] {
                    @include media('>=desktop') {
                        display: block;
                    }
                }
            }

            .pagination__controls__ellipsis {
                display: none;
            }

            [data-page='1'] {
                &:not([aria-expanded='true']) {
                    &:not([data-proximity='1']):not([data-proximity='-1']) {
                        & + .pagination__controls__ellipsis {
                            @include media('<tablet') {
                                display: flex;
                            }
                        }
                    }

                    &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']) {
                        & + .pagination__controls__ellipsis {
                            @include media('>=tablet', '<desktop') {
                                display: flex;
                            }
                        }
                    }

                    &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']):not([data-proximity='3']):not([data-proximity='-3']) {
                        & + .pagination__controls__ellipsis {
                            @include media('>=desktop') {
                                display: flex;
                            }
                        }
                    }
                }
            }

            .pagination__controls__button--penultimate {
                &:not([aria-expanded='true']) {
                    &:not([data-proximity='-1']) {
                        & + .pagination__controls__ellipsis {
                            @include media('<tablet') {
                                display: flex;
                            }
                        }
                    }

                    &:not([data-proximity='1']):not([data-proximity='-1']) {
                        & + .pagination__controls__ellipsis {
                            @include media('>=tablet', '<desktop') {
                                display: flex;
                            }
                        }
                    }

                    &:not([data-proximity='1']):not([data-proximity='-1']):not([data-proximity='2']):not([data-proximity='-2']) {
                        & + .pagination__controls__ellipsis {
                            @include media('>=desktop') {
                                display: flex;
                            }
                        }
                    }
                }
            }

            .pagination__controls__button--next,
            .pagination__controls__button--prev {
                span {
                    @include screenreader-only();
                }

                &:before {
                    @include font-family('icon');
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                }
            }

            .pagination__controls__button--next {
                margin-right: 0;

                &:before {
                    content: '\f061';
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                }
            }

            .pagination__controls__button--prev {
                &:before {
                    content: '\f060';
                    font-size: $font-size--icon;
                    font-weight: $font-weight-normal;
                }
            }
        }

        .pagination__summary {
            @include smalltype();
            margin-top: 0;
            max-width: unset;
            text-align: center;
        }
    }
}

.js .content .pagination__wrapper {
    @include media('screen') {
        max-width: $max-line-length;
    }
}
