////
/// Quotation
/// @group patterns
////
blockquote {
    .blockquote__body {
        padding-left: $measure--small1;
        padding-right: $measure--small1;
        padding-bottom: $measure--half;
        color: $color-gray11;

        & p {
            font-size: $font-size--gamma;
        }

        &--text-align-left {
            text-align: left;
        }

        &--text-align-right {
            text-align: right;
        }
        &--text-align-center {
            text-align: center;
        }

        svg {
            margin-top: $measure--small3;
            margin-bottom: $measure--small3;
        }

        & .svg--red {
            fill: $color-red50;
        }

        & .svg--black {
            fill: $color-nearblack;
        }
    }

    footer {
        padding-left: $measure--small1;
        margin-bottom: $measure--small3;
        text-align: left;
        font-size: $font-size--alpha; /// should this be small type rather??
    }
}
