////
/// CTA mixin
/// @group abstracts
////
@mixin cta-reset-properties() {
    display: inline-flex;
    position: relative;
    background: initial;
    color: initial;
    border: none;
    border-radius: $measure--quarter;
    padding: $measure--threequarters $measure--small2;
    font-weight: $font-weight-medium;
}

@mixin cta-reset-arrow-properties() {
    content: '\f061';
    font-family: 'Font Awesome';
    font-style: normal;
    font-weight: normal;
    text-decoration: inherit;
    opacity: 0;
    transform: translateX(-$measure--threequarters);
    position: absolute;
    right: $measure--small2;
    transition: all $transition-duration--long;
}

@mixin cta-reset() {
    @include cta-reset-properties();

    @include media('reducemotion') {
        transition: none;
    }

    @include media('<=mobile') {
        display: flex;
        justify-content: center;
        text-align: center;
    }

    @include media('hover') {
        transition: all $transition-duration--long;

        &:after {
            @include cta-reset-arrow-properties();

            @include media('reducemotion') {
                transition: none;
            }
        }

        &:focus,
        &:hover,
        &:active {
            padding: $measure--threequarters $measure--small2 + 0.5
                $measure--threequarters $measure--small2 - 0.5;
            color: $color-purewhite;
            background-color: transparent;
            border: 2px solid $color-red50;

            &:after {
                opacity: 1;
                transform: translateX($measure--threequarters);
            }
        }
    }
}

/**
 *
 * Call to action Primary style on a dark background
 * To use @include cta-primary-dark-bg();
 * this will style the CTA to defualt primary colours
 * 
 * to modify the colours pass in the background colour and colour as parameters
 * e.g @include cta-primary-dark-bg(yellow, green);
 * 
 *
 */

@mixin cta-primary-arrow-dark-bg(
    $bgColor: $color-brand-primary,
    $color: $color-bg
) {
    @include cta-reset();

    border: 2px solid $bgColor;
    background: $bgColor;
    color: $color;

    @include media('hover') {
        &:focus {
            color: $color;
            background: $bgColor;
        }

        &:active,
        &:focus {
            border-color: $bgColor;
            @include anchor-default-outline--bright();
        }

        &:hover {
            border-color: $bgColor;
        }

        &:disabled {
            color: rgba($color, 0.7);
        }
    }
}

@mixin cta-primary-dark-bg($bgColor: $color-brand-primary, $color: $color-bg) {
    @include cta-primary-arrow-dark-bg();

    @include media('hover') {
        background: $bgColor;
        color: $color;

        &:focus,
        &:active,
        &:hover {
            padding: $measure--threequarters $measure--small2;
        }

        &:after {
            content: '';
        }

        &:focus {
            background: $bgColor;
        }

        &:hover,
        &:active {
            border-color: $bgColor;
        }
    }
}

/**
 *
 * Call to action style for a light background
 * Primary colors will be used if no parameters is pass as args
 * e.g @include cta-primary-light-bg();
 * 
 * to modify the colours used pass in a backgound or colour arg
 * e.g @include cta-primary-light-bg(yellow, green);
 *
 */

@mixin cta-primary-arrow-light-bg(
    $lgbgColor: $color-brand-primary,
    $lgcolor: $color-purewhite
) {
    @include cta-reset();

    background: $lgbgColor;
    color: $lgcolor;
    border: 2px solid $lgbgColor;
    box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.15);

    @include media('hover') {
        &:focus {
            color: $lgcolor;
            background: $lgbgColor;
            border-color: $lgbgColor;
            @include anchor-default-outline--dark();
        }

        &:active {
            color: $lgbgColor;
            border-color: $lgbgColor;
            box-shadow: none;
            @include anchor-default-outline--dark();
        }

        &:hover {
            color: $lgbgColor;
            background: $lgcolor;
            border-color: $lgbgColor;
        }

        &:disabled {
            color: rgba($lgcolor, 0.7);
        }
    }
}

@mixin cta-primary-light-bg(
    $lgbgColor: $color-brand-primary,
    $lgcolor: $color-purewhite
) {
    @include cta-primary-arrow-light-bg();

    background: $lgbgColor;
    color: $lgcolor;

    @include media('hover') {
        &:after {
            content: '';
        }

        &:focus,
        &:active,
        &:hover {
            padding: $measure--threequarters $measure--small2;
        }

        &:focus {
            background: $lgbgColor;
        }

        &:hover,
        &:active {
            border-color: $lgbgColor;
        }
    }
}

/**
 *
 * Call to action outlined on a dark background
 * to use just call @include cta-outlined-dark-bg();
 *
 */

/**
 
     TODO:
     - Allow mixin to accept parameters to change default colours?
 
  */

@mixin cta-outlined-arrow-dark-bg() {
    @include cta-reset();

    background: transparent;
    color: $color-purewhite;
    border: 2px solid $color-purewhite;

    @include media('hover') {
        &:active,
        &:hover {
            color: $color-gray11;
            background-color: $color-purewhite;
            border-color: $color-purewhite;
        }

        &:focus {
            border-color: $color-purewhite;
            @include anchor-default-outline--bright();
        }

        &:active {
            @include anchor-default-outline--bright() border-color:
                $color-purewhite;
        }

        &:disabled {
            color: rgba($color-purewhite, 0.7);
        }
    }
}

@mixin cta-outlined-dark-bg() {
    @include cta-outlined-arrow-dark-bg();

    @include media('hover') {
        &:after {
            content: '';
        }

        &:focus,
        &:active,
        &:hover {
            padding: $measure--threequarters $measure--small2;
        }
    }
}

