.cta-block {
    list-style: none;
    margin: 0;

    .primary-cta-arrow--bright {
        @include cta-primary-arrow-light-bg();
    }

    .primary-cta--bright {
        @include cta-primary-light-bg();
    }

    .primary-cta-arrow--dark {
        @include cta-primary-arrow-dark-bg();
    }

    .primary-cta--dark {
        @include cta-primary-dark-bg();
    }

    .outline-cta-arrow--bright {
        @include cta-outlined-arrow-light-bg();
    }

    .outline-cta--bright {
        @include cta-outlined-light-bg();
    }

    .outline-cta-arrow--dark {
        @include cta-outlined-arrow-dark-bg();
    }

    .outline-cta--dark {
        @include cta-outlined-dark-bg();
    }

    .basic-cta--bright {
        @include cta-basic-light-bg();
    }

    .basic-cta--dark {
        @include cta-basic-dark-bg();
    }
}
