.youtube-video {
    .youtube-video__wrapper {
        height: 0;
        padding-bottom: 56.25%;
        position: relative;
        width: 100%;

        iframe {
            height: 100%;
            left: 0;
            margin: 0;
            position: absolute;
            top: 0;
            width: 100%;
        }
    }

    .youtube-video__caption-element--text,
    .youtube-video__caption-element--time {
        display: block;
    }
}
